@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
.navbar {
    width: 100%;
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: nowrap;
    justify-content: space-between;
    color: whitesmoke;
    background-color: #222326;
    padding: 8px;
}

.title {
    font-weight: 800;
    margin: 6px 0 5px 0;
}

.links {
    display: flex;
    padding-right: 15px;
    margin: 5px 0 5px 0;
}

.logo {
    display: flex;
}

.logo-img {
    padding-right: 10px;
    height: 60px;
}

li {
    display: block;
    padding: .5rem 1rem;
}

a {
    color: whitesmoke !important;
    text-decoration: none;
}

a:hover {
    color: #66d68e !important;
}

.main-logo {
    margin: auto;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 10%;
}

.homepage {
    display: flex;
}

.readme {
    color: #22ca5d !important;
}

h1 {
    text-align: center;
}

.main-text {
    margin: auto;
    width: 85%;
}

.input {
    margin-bottom: 10px;
}

.text-input {
    width: 35% !important;
}

.submit-img {
    height: 30px;
}

.main-content {
    display: flex;
    justify-content: space-between;
}

.column {
    width: 48%;
}

.column-30 {
    width: 30%;
}

hr {
    border-top: 3px solid #ababab !important;
    width: 95%;
}

/* .modal {
    width: 75%;
} */

.modal-li {
    padding: .2rem;
    display: list-item;
}

.modal-body {
    display: flex;
}

.uri-tooltip {
    text-decoration: underline;
}

.uri-tooltip:hover {
    color: #66d68e !important;    
}

.cover-img {
    height: 80px;
    border-radius: 3px;
    margin-right: 10px
}

.result {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.btn-outline-secondary {
    margin-bottom: 6px;
}

.submit {
    padding: 3px !important;
}

.buttons > * {
    margin-right: 4px;
}
.header {
    padding-right: 10px;
    font-size: 1.25em;
    font-weight: bold;
}

.main-content {
    display: flex;
    justify-content: space-between;
}

.column {
    width: 48%;
}

.MuiSlider-rail {
    background-color: #222326 !important;
}

.MuiSlider-thumb.Mui-disabled {
    height: 14px !important;
    width: 14px !important;
    background-color: #22ca5d !important;
    margin-top: -5px !important;
    margin-left: -6px !important;
}

.reduced-column {
    width: 46%;
}

.value-sliders {
    display: flex;
    justify-content: space-between;
}

.name {
    color: #22ca5d;
}

hr {
    border-top: 3px solid #ababab !important;
    width: 95%;
}
.App {
  font-family: "Open Sans",sans-serif;
}

.page {
  padding: 20px 30px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

